.logo {
    max-width: 500px;
    padding-top: 25px;
    display: block;
    margin-right: auto;
    margin-left: 25px;
}

.color {
  background-color: #235E6F
}
.navbar {
  background: linear-gradient(45deg, #0F8A5F, #235E6F);
  border: .2px solid #fff;
  height: 145px;
  display: flex !important;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  top: 0 !important;
  position: sticky !important;
  z-index: 15000;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1000px;
}

